  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  z-index: 3;
  background: #fff;
  border: none;
  border-radius: 10px;
  margin-bottom: 16px;
  padding: 10px;
  font-size: 1.5em;
  transition: width .4s ease-in-out;
  position: absolute;
  top: 10%;
  left: 10%;
  transform: translate(-10%, -10%);
}

#search-box:focus {
  width: 100%;
  background: #f7f709;
  border: none;
  border-radius: 10px;
  outline: none;
}

.country-card-style {
  max-width: 320px;
  z-index: 3;
